import React, { Suspense, useContext } from "react";
import Cookies from "js-cookie";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";
import {
  Box,
  Flex,
  IconButton,
  Button,
  HStack,
  Collapse,
  useColorModeValue,
  useDisclosure,
  Heading,
} from "@chakra-ui/react";

import DesktopNavbar from "./DesktopNavbar";
import MobileNavbar from "./MobileNavbar";
import ThemeSuspenese from "../../theme/ThemeSuspense";

import { UserContext } from "../../context/UserContext";
import { Outlet } from "react-router-dom";
import { useLogoutMutation } from "../../store/api-slice";

export default function Navbar() {
  const { isOpen, onToggle } = useDisclosure();

  const { state, dispatch } = useContext(UserContext);
  const { userInfo } = state;

  const [logout] = useLogoutMutation();

  const handleLogOut = async () => {
    await logout();
    dispatch({ type: "USER_LOGOUT" });
    Cookies.remove("userInfo");
  };

  return (
    <Suspense fallback={<ThemeSuspenese />}>
      <Box
        position="fixed"
        top={0}
        right={0}
        left={0}
        zIndex={2}
        borderBottom={1}
        borderStyle={"solid"}
        borderColor={useColorModeValue("gray.300", "gray.600")}>
        <Flex
          bg={useColorModeValue("white", "gray.800")}
          color={useColorModeValue("gray.600", "white")}
          minH={"60px"}
          py={{ base: 2 }}
          px={{ base: 4 }}
          align={"center"}
          maxWidth={1300}
          mx="auto">
          <Flex flex={{ base: 1 }} justify={{ base: "start" }}>
            <Logo />
            <Flex display={{ base: "none", md: "flex" }} ml={10}>
              <DesktopNavbar isAdmin={userInfo?.role === "admin"} />
            </Flex>
          </Flex>
          <Flex ml={{ base: -2 }}>
            <HStack>
              <Button
                size="sm"
                variant="outline"
                colorScheme="blue"
                onClick={handleLogOut}>
                Log out
              </Button>
              <Flex display={{ base: "flex", md: "none" }}>
                <IconButton
                  size="sm"
                  aria-label="Toggle Navigation"
                  onClick={onToggle}
                  icon={
                    isOpen ? (
                      <CloseIcon w={3} h={3} />
                    ) : (
                      <HamburgerIcon w={5} h={5} />
                    )
                  }
                />
              </Flex>
            </HStack>
          </Flex>
        </Flex>
        <Collapse in={isOpen} animateOpacity>
          <MobileNavbar isAdmin={userInfo?.role === "admin"} />
        </Collapse>
      </Box>
      <Outlet />
    </Suspense>
  );
}

const Logo = () => {
  return (
    <Flex align="center">
      <Heading size="md">BOOKING</Heading>
    </Flex>
  );
};
